<template>
	<view class="content">
		<div class="scanBox" @longtap="saveScan">
			<image class="scan" :src="scan_src" mode="widthFix"></image>
		</div>
		<view class="scanName">长按保存图片</view>
		
		<div class="footer">
			<view class="rows" @tap="share(0)">
				<uni-icons class="icon" type="weixin" color="grey" size="22"/>
				<view class="names">微信</view>
			</view>
			<view class="rows" @tap="share(1)">
				<uni-icons class="icon" type="pengyouquan" color="grey" size="22"/>
				<view class="names">朋友圈</view>
			</view>
		</div>
		
		
	</view>
</template>
<style>
	.content{overflow-y: hidden;}
	.content .scanBox{width:100%;height:85vh}
	.content .scanName{width:100%;text-align: center;font-size: 14px;color:white;margin-top: 20upx;position: absolute;top:55%;}
	.content .scan{width:100%;height:100%;}
	.content .footer{width:100%;position:absolute;left:0;bottom:2%;text-align: center;}
	.content .footer .rows{width:50%;float: left;}
	.content .footer .rows .names{font-size: 14px;}

</style>
<script>
	export default {
		data() {
			return {
				scan_src:"",
				
				
			}
		},
		onLoad(){
			this.getStorage();
			this.AjaxLoad()
		},
		methods: {
			getStorage(){var that = this;try {const value = uni.getStorageSync('token');if (value) {that._$token = value}} catch (e) {}},
			AjaxLoad(){
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
				};
				this._$InitRequest.request('user/get_userInfo', param).then(res => {
					this.scan_src = res.data.data.code_url
				},error=>{})
			},
			share(index){
				if( index == 0 ){
					uni.share({
					    provider: "weixin",
					    scene: "WXSceneSession",
					    type: 2,
					    imageUrl: this.scan_src,
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    },
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
				}else if( index == 1 ){
					uni.share({
					    provider: "weixin",
					    scene: "WXSenceTimeline",
					    type: 2,
					    imageUrl: this.scan_src,
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    },
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
				}
			},
			saveScan(){
				uni.saveImageToPhotosAlbum({
					filePath: this.scan_src,
					success: function () {
						uni.showToast({icon:"none",title:"保存成功"})
					}
				});
			},
			
			
			
			
		}
	}
</script>
<style lang="scss" scoped>
/deep/ .uni-input-input {font-size:32upx}
/deep/ .uni-radio-input-checked{background-color:rgb(110,43,34) !important;border:2upx solid white !important}
.content .html{
  ::v-deep {
    img {
      width: 100%;
    }
  }
}
</style>